import Preloader from '../../common/Preloader/Preloader';
import s from './ProfileInfo.module.css';
import ProfileStatusWithHooks from './ProfileStatusWithHooks';

export default function ProfileInfo(props) {
  if (!props.profileData) {
    return <Preloader />;
  }
  return (
    <div>
      <div className={s.profileBackground}>
        <img alt='' src='https://helpx.adobe.com/content/dam/help/en/photoshop/using/convert-color-image-black-white/jcr_content/main-pars/before_and_after/image-before/Landscape-Color.jpg' />
      </div>
      <div className={s.descriptionBlock}>
        <img alt="" src={props.profileData.photos.large} />
        {props.profileData.fullName}
        <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus} />
        </div>
    </div>
  )
}