import s from './MyPosts.module.css';
import Post from './Post/Post';
import React from 'react';

export default function MyPosts(props) {
  const postElements = props.postsData.map(data => <Post message={data.message} likesCount={data.likesCount} key={data.id} />);

  return (
    <div className={s.postsBlock}>
      <h3>My posts</h3>
      <div>
        <div><textarea onChange={props.updateNewPostText} value={props.newPostText} placeholder='Type new post here...' /></div>
        <div><button onClick={props.addPost}>Add post</button></div>
      </div>
      <div className={s.posts}>
        {postElements}
      </div>
    </div>
  )
}